<template>
  <transition name="slide">
    <div class="add-song" v-show="showFlag" @click.stop>
      <div class="header">
        <h1 class="title">添加歌曲到列表</h1>
        <div class="close" @click="hide">
          <i class="icon-close"></i>
        </div>
      </div>
      <div class="search-box-wrapper">
        <search-box ref="searchBox" @query="onQueryChange" placeholder="搜索歌曲"></search-box>
      </div>
      <div class="shortcut" v-show="!query">
        <switches :switches="switches" :currentIndex="currentIndex" @switch="switchItem"></switches>
        <div class="list-wrapper">
          <scroll class="list-scroll" v-if="currentIndex === 0" :data="playHistory" ref="playHistory">
            <div class="list-inner">
              <song-list :songs="playHistory" @select="selectPlay"></song-list>
            </div>
          </scroll>
          <scroll class="list-scroll" v-if="currentIndex === 1" :data="searchHistory" ref="searchHistory" :refreshDelay="100">
            <div class="list-inner">
              <search-list @delete="deleteOneSearch" @select="addKey" :searches="searchHistory"></search-list>
            </div>
          </scroll>
        </div>
      </div>
      <div class="search-result" v-show="query">
        <suggest @select="selectSuggest" @listScrollBefore="blurInput" ref="suggest" :query="query" :showSinger="false"></suggest>
      </div>
      <top-tip ref="topTip">
        <div class="tip-title">
          <i class="icon-ok"></i>
          <span class="text">1首歌曲已经添加到播放列表</span>
        </div>
      </top-tip>
    </div>
  </transition>
</template>

<script type="text/ecmascript-6">
  import SearchBox from 'base/search-box/search-box';
  import Suggest from 'components/suggest/suggest';
  import Switches from 'base/switches/switches';
  import SongList from 'base/song-list/song-list';
  import Scroll from 'base/scroll/scroll';
  import SearchList from 'base/search-list/search-list';
  import TopTip from 'base/top-tip/top-tip';
  import {searchMixin} from 'common/js/mixin';
  import {mapGetters, mapActions} from 'vuex';
  export default {
    mixins: [searchMixin],
    data() {
      return {
        showFlag: false,
        switches: ['最近播放', '搜索历史'],
        currentIndex: 0
      };
    },
    methods: {
      show() {
        this.showFlag = true;
        setTimeout(() => {
          if (this.currentIndex === 0) {
            this.$refs.playHistory.refresh();
          } else {
            this.$refs.searchHistory.refresh();
          }
        }, 20);
      },
      hide() {
        this.showFlag = false;
      },
      switchItem(index) { // 切换switch
        this.currentIndex = index;
      },
      selectPlay(song) { // 播放最近播放歌曲
        this.insertSong(song);
        this.showTopTip();
      },
      selectSuggest() { // 搜索列表点击
        this.saveSearch();
        this.showTopTip();
      },
      showTopTip() { // 显示顶部提示框
        this.$refs.topTip.show();
      },
      ...mapActions([
        'insertSong'
      ])
    },
    computed: {
      ...mapGetters([
        'playHistory',
        'searchHistory'
      ])
    },
    watch: {
      query(newQuery) {
        if (!newQuery) {
          setTimeout(() => {
            if (this.currentIndex === 0) {
              this.$refs.playHistory.refresh();
            } else {
              this.$refs.searchHistory.refresh();
            }
          }, 20);
        };
      }
    },
    components: {
      SearchBox,
      Suggest,
      Switches,
      SongList,
      Scroll,
      SearchList,
      TopTip
    }
  };
</script>

<style scoped lang="less" rel="stylesheet/less">
  @import "~common/less/variable";
  @import "~common/less/mixin";

  .add-song{
    position: fixed;
    top: 0;
    bottom: 0;
    width: 100%;
    z-index: 200;
    background: @color-background;
    &.slide-enter-active, &.slide-leave-active{
      transition: all 0.3s;
    }
    &.slide-enter, &.slide-leave-to{
      transform: translate3d(100%, 0, 0);
    }
    .header{
      position: relative;
      height: 44px;
      text-align: center;
      .title{
        line-height: 44px;
        font-size: @font-size-large;
        color: @color-text;
      }
      .close{
        position: absolute;
        top: 0;
        right: 8px;
        .icon-close{
          display: block;
          padding: 12px;
          font-size: 20px;
          color: @color-theme;
        }
      }
    }
    .search-box-wrapper{
      margin: 20px;
    }
    .shortcut{
      .list-wrapper{
        position: absolute;
        top: 165px;
        bottom: 0;
        width: 100%;
        .list-scroll{
          height: 100%;
          overflow: hidden;
          .list-inner{
            padding: 20px 30px;
          }
        }
      }
    }
    .search-result{
      position: fixed;
      top: 124px;
      bottom: 0;
      width: 100%;
    }
    .tip-title{
      text-align: center;
      padding: 18px 0;
      font-size: 0;
      .icon-ok{
        font-size: @font-size-medium;
        color: @color-theme;
        margin-right: 4px;
      }
      .text{
        font-size: @font-size-medium;
        color: @color-text;
      }
    }
  }
</style>


